import config from './config'

const api = {
  getDogs() {
    return fetch(`${config.REACT_APP_API_BASE}/dogs`)
      .then((dogs) => {
      if (!dogs.ok) {
        throw new Error('No dogs left')
      }
      return dogs.json();
    })
},

  getCats() {
    return fetch(`${config.REACT_APP_API_BASE}/cats`)
      .then((cats) => {
        if (!cats.ok) {
          throw new Error('No cats left')
        }
        return cats.json();
      })
},

  getPeople() {
    return fetch(`${config.REACT_APP_API_BASE}/people`)
      .then((people) => {
      if (!people.ok) {
        throw new Error('No people')
      }
      return people.json();
    })
},

  addPerson(name) {
    return fetch(`${config.REACT_APP_API_BASE}/people`, {
      method: "POST",
      headers: {
        "Content-Type": "applicatsion/json",
      },
      body: JSON.stringify({
        person: name,
      }),
    })
      .then((res) =>
        !res.ok ? res.json().then((e) => Promise.reject(e)) : res.json()
      )
  },

  deleteDogs() {
    return fetch(`${config.REACT_APP_API_BASE}/dogs`, {
      method: "DELETE",
    })
      .then((res) =>
        !res.ok ? res.json().then((e) => Promise.reject(e)) : res.json()
      )
},

  deleteCats() {
    return fetch(`${config.REACT_APP_API_BASE}/catss`, {
      method: "DELETE",
    })
      .then((res) =>
        !res.ok ? res.json().then((e) => Promise.reject(e)) : res.json()
      )
},

  deletePerson(person) {
    return fetch(`${config.REACT_APP_API_BASE}/people`, {
    method: "DELETE",
    headers: {
      "Content-Type": "applicatsion/json",
    },
    body: JSON.stringify({
      name: person,
    }),
  })
    .then((res) =>
      !res.ok ? res.json().then((e) => Promise.reject(e)) : res.json()
    )
  },
}

export default api